<template>
  <div id="Shows">
    <TopShows
      v-if="showsList.length"
      :shows-list="showsList"
    />
    <div
      v-for="(genre, key) in genres"
      :key="key"
    >
      <v-hover v-slot:default="{ hover }">
        <div
          class="mx-md-12 mt-md-9 pl-md-1 mt-2 pointer"
          @click="showSelectedGenre(genre)"
        >
          <span
            :class="{'light':!$vuetify.theme.dark,'dark':$vuetify.theme.dark}"
            class="pl-5"
          >
            {{ genre
            }}
          </span>
          <v-btn
            v-if="hover"
            class="overline no-background-hover"
            :text="true"
            :tile="false"
            :ripple="false"
            small
          >
            Explore All
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
        </div>
      </v-hover>

      <div class="container">
        <v-col
          v-for="(list, slideKey) in filterByGenre(showsList, genre).slice(0, 12)"
          :key="slideKey"
          class="pa-0 pr-1"
          md="2"
          sm="3"
          cols="5"
        >
          <Cards
            :list="list"
            :image="list.image.medium"
          />
        </v-col>
      </div>
    </div>
  </div>
</template>
<script>
import { genreFilter } from "@/filters/filter-by-genre.js";
import Cards from "@/components/card-for-shows/Cards.vue";
import TopShows from "@/components/top-shows/TopShows.vue";
import { mapGetters } from "vuex";
import getAllShows from "@/mixins/fetch-shows.js";

export default {
  name: "Shows",
  components: {
    Cards,
    TopShows,
  },
  mixins: [getAllShows],
  data() {
    return {
      genres: [
        "Action",
        "Horror",
        "Comedy",
        "Drama",
        "Romance",
        "Adventure",
        "Thriller",
        "Science-Fiction",
      ],
    };
  },
  computed: {
    ...mapGetters({ showsList: "allShowsList" }),
  },
  created() {
    this.getAllShows();
  },
  methods: {
    filterByGenre(list, genre) {
      return genreFilter(list, genre);
    },
    showSelectedGenre(genre) {
      this.$router.push({ name: "showByGenre", params: { id: genre } });
    },
    moreDetails(showId) {
      this.$router.push({ name: "showDetails", params: { id: showId } });
    },
  },
};
</script>

<style scoped>
.no-background-hover::before {
  background-color: transparent;
}
</style>



