<template>
  <div id="genre">
    <v-container>
      <div
        class="headline mx-5 mt-5"
        :class="{'light':!$vuetify.theme.dark,'dark':$vuetify.theme.dark}"
      >
        <v-row>
          <v-col>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon
                  @click="$router.go(-1)"
                  v-on="on"
                >
                  mdi-arrow-left
                </v-icon>
                {{ genre }}
              </template>
              <span>Back</span>
            </v-tooltip>
          </v-col>
        </v-row>

        <v-row>
          <v-col
            v-for="list in filteredGenre"
            :key="list.id"
            md="2"
            sm="4"
            cols="6"
          >
            <Cards
              :list="list"
              :image="list.image.medium"
              size="90%"
            />
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
import { genreFilter } from "@/filters/filter-by-genre.js";
import Cards from "@/components/card-for-shows/Cards.vue";
import getAllShows from "@/mixins/fetch-shows.js";
import { mapGetters } from "vuex";
export default {
  name: "ShowByGenre",
  components: {
    Cards,
  },
  mixins: [getAllShows],
  data() {
    return {
      genre: this.$route.params.id,
      filteredGenre: {},
    };
  },
  computed: {
    ...mapGetters({ showsList: "allShowsList" }),
  },
  created() {
    this.getFilter();
  },

  methods: {
    filterByGenre(list, genre) {
      return genreFilter(list, genre);
    },

    async getFilter() {
      if (!this.showsList.length) {
        await this.getAllShows();
      }
      this.filteredGenre = this.filterByGenre(this.showsList, this.genre);
    },
  },
};
</script>

