<template>
  <div id="header">
    <v-card>
      <v-row>
        <v-col md="3" sm="4" cols="8" class="pl-10">
          <v-toolbar-title>
            <router-link
              to="/"
              :class="{'light':!$vuetify.theme.dark,'dark':$vuetify.theme.dark}"
            >TV Shows</router-link>
          </v-toolbar-title>
        </v-col>
        <v-spacer class="hidden-sm-and-down" />
        <v-col class="hidden-sm-and-up" cols="4" align="end" justify="end" align-self="end">
          <v-switch v-model="dark" label="Dark" color="white" class="mt-0" />
        </v-col>
        <v-col md="3" sm="4" cols="8">
          <v-text-field
            v-if="showSearchBar"
            v-model="searchQuery"
            rounded
            outlined
            dense
            label="Search Shows"
            clearable
            @input="searchQueryChanged"
          />
        </v-col>

        <v-col md="1" sm="1" cols="4">
          <v-btn icon>
            <v-icon @click="showSearchBar = !showSearchBar">mdi-magnify</v-icon>
          </v-btn>
        </v-col>
        <v-col md="2" sm="2" align="end" class="hidden-xs-only">
          <v-switch v-model="dark" label="Dark" color="white" class="mt-0" />
        </v-col>
      </v-row>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      showSearchBar: false,
      searchQuery: "",
      dark: false,
    };
  },
  watch: {
    dark() {
      this.$vuetify.theme.dark = this.dark;
    },
  },
  methods: {
    searchQueryChanged() {
      if (this.searchQuery === null) {
        this.searchQuery = "";
      }
      if (this.searchQuery.length) {
        this.$router.push({ name: "search", params: { id: this.searchQuery } });
      } else if (!this.searchQuery.length && this.$route.name !== "home") {
        this.$router.push("/");
      }
    },
  },
};
</script>

<style scoped>
.padding {
  padding-left: 15%;
}
.dark {
  text-decoration: none;
  color: white;
}
.light {
  text-decoration: none;
  color: black;
}
</style>
