<template>
  <div>
    <div class="hidden-xs-only">
      <v-container>
        <v-carousel>
          <v-carousel-item
            v-for="(list, i) in getTopShows"
            :key="i"
          >
            <v-img
              :src="list.image.original"
              min-height="550"
              gradient="to left, rgba(255,255,255,0.3), rgba(0,0,0,0.85),rgba(0,0,0,0.95), rgba(0,0,0,0.98)"
              class="pointer"
              @click="moreDetails(list.id)"
            >
              <v-col
                class="ml-md-2"
                justify="center"
                md="9"
                sm="10"
              >
                <Summary :show-detail="list" />
              </v-col>
            </v-img>
          </v-carousel-item>
        </v-carousel>
      </v-container>
    </div>
    <div
      class="hidden-sm-and-up mb-0 pb-0"
      align="center"
      justify="center"
    >
      <v-row>
        <v-col
          cols="12"
          class="mb-0 pb-0"
        >
          <v-carousel
            cycle
            hide-delimiters
            show-arrows
          >
            <v-carousel-item
              v-for="(list, i) in getTopShows"
              :key="i"
            >
              <appCards
                :list="list"
                :image="list.image.original"
                size="90%"
              />
            </v-carousel-item>
          </v-carousel>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import Summary from "@/components/show-summary/Summary.vue";
import appCards from "@/components/card-for-shows/Cards.vue";
export default {
  name: "TopShows",
  components: {
    appCards,
    Summary,
  },
  props: {
    showsList: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    getTopShows() {
      let localList = [...this.showsList];
      localList.sort((first, second) => {
        return second.rating.average - first.rating.average;
      });
      return localList.slice(0, 6);
    },
  },
  methods: {
    moreDetails(showId) {
      this.$router.push({ name: "showDetails", params: { id: showId } });
    },
  },
};
</script>
