<template>
  <div
    v-if="showDetail.id"
    id="showDetails"
  >
    <v-icon
      class="ml-4 ma-1 mt-3"
      @click="$router.go(-1)"
    >
      mdi-arrow-left
    </v-icon>
    <v-icon
      class="ml-4 ma-1 mt-3"
      @click="$router.push('/')"
    >
      mdi-home
    </v-icon>
    <v-row>
      <v-col
        class="pl-md-10 pl-sm-8"
        md="11"
        sm="11"
      >
        <div class="hidden-xs-only">
          <v-img
            :src="showDetail.image.original"
            max-height="550"
            gradient="to left, rgba(255,255,255,0.3), rgba(0,0,0,0.85),rgba(0,0,0,0.95), rgba(0,0,0,0.98)"
            class="align-left"
          >
            <v-col
              class="ml-md-2"
              justify="center"
              md="9"
              sm="10"
            >
              <Summary :show-detail="showDetail" />
            </v-col>
          </v-img>
        </div>
        <div class="hidden-sm-and-up">
          <v-img
            :src="showDetail.image.original"
            class="mx-5"
          />
          <v-col>
            <Summary
              :show-detail="showDetail"
              :theme="theme"
            />
          </v-col>
        </div>
      </v-col>
    </v-row>

    <div
      class="headline ml-6 mt-md-4 mb-2"
      @click="showEpisodes = !showEpisodes"
    >
      Episodes
      <v-icon>
        {{
          showEpisodes ? "mdi-chevron-up" : "mdi-chevron-down"
        }}
      </v-icon>
    </div>

    <div v-if="showDetail._embedded.episodes.length && showEpisodes">
      <v-row class="ml-5">
        <v-col
          cols="7"
          md="2"
          sm="4"
        >
          <v-select
            v-model="season"
            :items="getSeasons"
            placeholder="Season 1"
          />
        </v-col>
      </v-row>

      <EpisodeDetail :details="getEpisodes" />
    </div>

    <div
      class="headline ml-6 mt-md-4 mb-2"
      @click="showCast = !showCast"
    >
      Cast
      <v-icon>{{ showCast ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
    </div>
    <Team
      v-if="showCast"
      :details="showDetail._embedded.cast"
    />

    <div
      class="headline ml-6 mt-md-4 mb-2 "
      @click="showCrew = !showCrew"
    >
      Crew
      <v-icon>{{ showCrew ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
    </div>

    <Team
      v-if="showCrew"
      :details="showDetail._embedded.crew"
    />
  </div>
</template>
<script>
import { showDetails } from "../../service/index.js";
import Summary from "@/components/show-summary/Summary.vue";
import Team from "@/components/cast-and-crew/Team.vue";
import EpisodeDetail from "@/components/episodes/EpisodeDetails.vue";

export default {
  name: "ShowDetails",
  components: {
    Summary,
    Team,
    EpisodeDetail,
  },
  data() {
    return {
      showId: this.$route.params.id,
      showDetail: {},
      showCast: false,
      showCrew: false,
      showEpisodes: false,
      season: "season 1",
    };
  },
  computed: {
    getSeasons() {
      let noOfSeason = this.showDetail._embedded.episodes[
        this.showDetail._embedded.episodes.length - 1
      ].season;
      let seasonList = [];
      for (let iterator = 0; iterator < noOfSeason; iterator++) {
        seasonList.push(`Season ${iterator + 1}`);
      }
      return seasonList;
    },
    getEpisodes() {
      let episodes = [];
      this.showDetail._embedded.episodes.forEach((element) => {
        if (element.season === Number(this.season.substring(7, 8))) {
          episodes.push(element);
        }
      });

      return episodes;
    },
    theme() {
      if (this.$vuetify.theme.dark) return "white--text";
      else {
        return "black--text";
      }
    },
  },
  created() {
    this.getShowDetails();
  },
  methods: {
    getShowDetails() {
      const details = showDetails(this.showId);
      details
        .then((response) => {
          this.showDetail = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

