import { listOfAllShows } from "@/service/index.js";
const getAllShows = {
  methods: {
    async getAllShows() {
      try {
        const showsListPromise = await listOfAllShows();
        this.$store.dispatch("setAllShowsList", showsListPromise.data);
      } catch (error) {
        console.log(error);
      }
    }
  }
}
export default getAllShows;