<template>
  <div id="search">
    <v-icon
      class="pt-3 pl-3"
      @click="$router.push('/')"
    >
      mdi-arrow-left
    </v-icon>
    <v-container>
      <v-row>
        <v-col
          v-for="list in searchQuery()"
          :key="list.id"
          md="2"
          sm="3"
          cols="6"
        >
          <Cards
            :list="list"
            :image="list.image.medium"
            size="100%"
          />
        </v-col>
        <div
          v-if="!searchQuery().length"
          class="pa-4"
        >
          Sorry, No Show found!
        </div>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { showFilter } from "@/filters/filter-by-shows.js";
import Cards from "@/components/card-for-shows/Cards.vue";
import { mapGetters } from "vuex";
import getAllShows from "@/mixins/fetch-shows.js";

export default {
  name: "Search",
  components: {
    Cards,
  },
  mixins: [getAllShows],
  data() {
    return {
      showId: this.$route.params.id,
    };
  },
  computed: {
    ...mapGetters({ showList: "allShowsList" }),
  },
  methods: {
    searchQuery() {
      this.showId = this.$route.params.id;
      if (!this.showList.length) {
        this.getAllShows();
      }
      return showFilter(this.showList, this.showId);
    },
  },
};
</script>

