<template>
  <div id="Cards">
    <v-hover v-slot:default="{ hover }">
      <v-card
        :width="size"
        :elevation="hover ? 24 : 1"
        :outlined="hover"
        :light="hover"
        class="ml-1 mb-2 mt-1"
        @click="moreDetails(list.id)"
      >
        <v-img
          :src="image"
          class="align-end"
        >
          <div class="on-hover">
            <v-card-title
              v-if="hover"
              class="align-top"
            >
              <span class="body-2 white--text">
                {{
                  list.name.toUpperCase()
                }}
              </span>
            </v-card-title>
            <v-card-text v-if="hover">
              <span
                v-for="genre in list.genres"
                :key="genre"
                class="overline white--text"
              >
                {{ genre }}
                <br>
              </span>
              <v-rating
                v-model="rating"
                readonly
                color="yellow"
                background-color="white darken-2"
                small
                half-increments
              />
              <span class="white--text">RATING: {{ rating }}/5</span>
            </v-card-text>
          </div>
        </v-img>
      </v-card>
    </v-hover>
  </div>
</template>
<script>
export default {
  name: "Cards",
  props: {
    list: {
      type: Object,
      required: true,
    },
    image: {
      type: String,
    },
    size: {
      type: String,
    },
  },
  computed: {
    rating() {
      return this.list.rating.average / 2;
    },
  },
  methods: {
    moreDetails(showId) {
      this.$router.push({ name: "showDetails", params: { id: showId } });
    },
  },
};
</script>

<style scoped>
.on-hover {
  background-image: linear-gradient(
    to bottom,
    rgba(43, 42, 42, 0.4),
    rgba(39, 38, 38, 0.7),
    rgba(0, 0, 0, 0.95),
    rgba(0, 0, 0, 0.98),
    rgba(0, 0, 0, 1)
  );
}
</style>
