import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import showByGenre from "@/views/shows-by-genre/ShowByGenre.vue";
import search from "@/views/search-shows/Search.vue";
import showDetails from "@/views/details-of-show/ShowDetails.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/genre/:id",
    name: "showByGenre",
    component: showByGenre,
  },

  {
    path: "/search/show/:id",
    name: "search",
    component: search,
  },
  {
    path: "/show/:id",
    name: "showDetails",
    component: showDetails,
  },
  {
    path: "*",
    name: "invalidURL",
    component: () => import(/* webpackChunkName: "errorPage" */ '@/views/error-pages/InvalidURL.vue')
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
