import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allShowsList: [],
  },
  mutations: {
    allShowsList: (state, list) => {
      state.allShowsList = list;
    },
  },
  actions: {
    setAllShowsList({ commit }, list) {
      commit('allShowsList', list);
    }
  },
  modules: {},
  getters: {
    allShowsList: (state) => state.allShowsList,
  },
});
